* {
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  font-weight: 400;
  font-size: 2rem;
}

h2 {
  font-weight: 400;
}

h3 {
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
}

h4 {
  text-align: left;
  padding: 0 0 .5rem;
}

h5 {
  font-weight: 400;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
}

h6 {
  font-size: .7rem;
  font-weight: 700;
}

ul {
  list-style: none;
}

li {
  margin-top: 1rem;
}

.container {
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  margin: auto;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-top: 2rem;
}

.clear {
  display: block;
  -ms-flex: none;
      flex: none;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 0 .5rem;
}

.row-basic {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.link {
  text-decoration: underline;
  cursor: pointer;
}

select {
  -webkit-appearance: none;
  border: solid 1px #333;
  border-radius: 1px;
  padding: .25rem .5rem;
  margin-left: .5rem;
}

select:focus, input[type="text"]:focus {
  outline: none;
}

input[type="text"] {
  padding: .25rem .5rem;
  margin-left: .5rem;
  border: none;
  border-bottom: solid 1px black;
  text-align: left;
  font-size: .8rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.submit {
  padding: .5rem 1rem;
  border-radius: .1rem;
  font-size: .7rem;
  text-transform: uppercase;
  letter-spacing: .1rem;
  background: #1B8CCF;
  color: white;
  outline: none;
  border: solid 1px black;
  cursor: pointer;
}

.h1Input {
  border: none !important;
  border-bottom: solid 1px black !important;
  font-weight: 400 !important;
  font-size: 2rem !important;
  width: 20rem !important;
  text-align: center !important;
  margin-bottom: 2rem;
}

.underline {
  text-decoration: underline;
}

.error {
  color: red;
}



